import React, { useState } from 'react';

interface IdeologyCardProps {
  card: any;
  onAnswer: (answer: string) => void;
  onRedraw: () => void;
  currentPlayer: any;
}

export default function IdeologyCard({ card, onAnswer, onRedraw, currentPlayer }: IdeologyCardProps) {
  const [showRedrawConfirm, setShowRedrawConfirm] = useState(false);

  if (!card) return null;

  const canRedraw = () => {
    if (!currentPlayer) return false;
    const totalResources = currentPlayer.resources.funds + currentPlayer.resources.clout + 
                          currentPlayer.resources.media + currentPlayer.resources.trust;
    return totalResources >= 4;
  };

  const getResourceDisplay = (resources: any) => {
    return Object.entries(resources).map(([resource, amount]) => (
      <span key={resource} className="inline-block bg-primary/10 text-primary px-2 py-1 rounded-md text-xs font-medium">
        {resource}: +{amount as number}
      </span>
    ));
  };

  const handleRedraw = () => {
    onRedraw();
    setShowRedrawConfirm(false);
  };

  const getIdeologyColor = (ideology: string) => {
    const colors = {
      capitalist: 'border-blue-200 hover:border-blue-300 hover:bg-blue-50',
      supremo: 'border-red-200 hover:border-red-300 hover:bg-red-50',
      showstopper: 'border-purple-200 hover:border-purple-300 hover:bg-purple-50',
      idealist: 'border-green-200 hover:border-green-300 hover:bg-green-50'
    };
    return colors[ideology as keyof typeof colors] || 'border-gray-200 hover:border-gray-300 hover:bg-gray-50';
  };

  const getIdeologyTextColor = (ideology: string) => {
    const colors = {
      capitalist: 'text-blue-700',
      supremo: 'text-red-700',
      showstopper: 'text-purple-700',
      idealist: 'text-green-700'
    };
    return colors[ideology as keyof typeof colors] || 'text-gray-700';
  };

  return (
    <div className="bg-card border rounded-lg p-6 shadow-sm">
      <div className="flex justify-between items-start mb-6">
        <div className="space-y-1">
          <h3 className="text-xl font-bold tracking-tight">Ideology Card</h3>
          <p className="text-sm text-muted-foreground">Choose your political stance</p>
        </div>
        
        {canRedraw() && (
          <div className="relative">
            <button
              onClick={() => setShowRedrawConfirm(true)}
              className="px-3 py-2 text-sm bg-muted text-muted-foreground rounded-md hover:bg-muted/80 transition-colors font-medium"
            >
              Redraw (4 resources)
            </button>
            
            {showRedrawConfirm && (
              <div className="absolute right-0 top-full mt-2 bg-card border rounded-lg p-3 shadow-lg z-10 min-w-48">
                <p className="text-sm mb-3">Spend 4 resources to redraw this card?</p>
                <div className="flex space-x-2">
                  <button
                    onClick={handleRedraw}
                    className="px-3 py-1 text-xs bg-destructive text-destructive-foreground rounded hover:bg-destructive/90"
                  >
                    Confirm
                  </button>
                  <button
                    onClick={() => setShowRedrawConfirm(false)}
                    className="px-3 py-1 text-xs bg-secondary text-secondary-foreground rounded hover:bg-secondary/80"
                  >
                    Cancel
                  </button>
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      <div className="mb-6">
        <div className="p-4 bg-muted/30 rounded-lg border-l-4 border-primary">
          <h4 className="font-semibold text-lg mb-2">Political Question</h4>
          <p className="text-foreground">{card.question}</p>
        </div>
      </div>

      <div className="space-y-4">
        {/* Capitalist Option */}
        {card.capitalist && (
          <div 
            className={`p-4 border-2 rounded-lg cursor-pointer transition-all duration-200 ${getIdeologyColor('capitalist')}`}
            onClick={() => onAnswer('capitalist')}
          >
            <div className="flex justify-between items-start mb-3">
              <span className={`text-sm font-bold ${getIdeologyTextColor('capitalist')}`}>
                The Capitalist
              </span>
              <div className="flex flex-wrap gap-2">
                {getResourceDisplay(card.capitalist.resources)}
              </div>
            </div>
            <p className="text-sm text-foreground leading-relaxed">{card.capitalist.text}</p>
          </div>
        )}

        {/* Supremo Option */}
        {card.supremo && (
          <div 
            className={`p-4 border-2 rounded-lg cursor-pointer transition-all duration-200 ${getIdeologyColor('supremo')}`}
            onClick={() => onAnswer('supremo')}
          >
            <div className="flex justify-between items-start mb-3">
              <span className={`text-sm font-bold ${getIdeologyTextColor('supremo')}`}>
                The Supremo
              </span>
              <div className="flex flex-wrap gap-2">
                {getResourceDisplay(card.supremo.resources)}
              </div>
            </div>
            <p className="text-sm text-foreground leading-relaxed">{card.supremo.text}</p>
          </div>
        )}

        {/* Showstopper Option */}
        {card.showstopper && (
          <div 
            className={`p-4 border-2 rounded-lg cursor-pointer transition-all duration-200 ${getIdeologyColor('showstopper')}`}
            onClick={() => onAnswer('showstopper')}
          >
            <div className="flex justify-between items-start mb-3">
              <span className={`text-sm font-bold ${getIdeologyTextColor('showstopper')}`}>
                The Showstopper
              </span>
              <div className="flex flex-wrap gap-2">
                {getResourceDisplay(card.showstopper.resources)}
              </div>
            </div>
            <p className="text-sm text-foreground leading-relaxed">{card.showstopper.text}</p>
          </div>
        )}

        {/* Idealist Option */}
        {card.idealist && (
          <div 
            className={`p-4 border-2 rounded-lg cursor-pointer transition-all duration-200 ${getIdeologyColor('idealist')}`}
            onClick={() => onAnswer('idealist')}
          >
            <div className="flex justify-between items-start mb-3">
              <span className={`text-sm font-bold ${getIdeologyTextColor('idealist')}`}>
                The Idealist
              </span>
              <div className="flex flex-wrap gap-2">
                {getResourceDisplay(card.idealist.resources)}
              </div>
            </div>
            <p className="text-sm text-foreground leading-relaxed">{card.idealist.text}</p>
          </div>
        )}
      </div>

      <div className="mt-6 p-3 bg-muted/20 rounded-lg">
        <p className="text-xs text-muted-foreground text-center">
          Choose your answer to receive resources and build your ideological power. Each choice shapes your political identity.
        </p>
      </div>
    </div>
  );
}
