import React, { useState } from 'react';
import Zone from './Zone';

interface GameBoardProps {
  gameState: any;
  sendMessage: (msg: any) => void;
  isCurrentTurn: boolean;
  playerId: string | null;
}

export default function GameBoard({ gameState, sendMessage, isCurrentTurn, playerId }: GameBoardProps) {
  const [selectedZone, setSelectedZone] = useState<number | null>(null);
  const [actionMode, setActionMode] = useState<'place' | 'gerrymander' | null>(null);
  const [gerrymanderFrom, setGerrymanderFrom] = useState<number | null>(null);

  if (!gameState || !gameState.zones) {
    return (
      <div className="bg-card border rounded-lg p-6 shadow-sm">
        <div className="flex items-center justify-center h-64">
          <div className="text-center space-y-3">
            <div className="text-4xl">üé≤</div>
            <p className="text-muted-foreground">Loading game board...</p>
          </div>
        </div>
      </div>
    );
  }

  const handleZoneClick = (zoneId: number) => {
    if (!isCurrentTurn) return;

    if (actionMode === 'place') {
      setSelectedZone(zoneId);
    } else if (actionMode === 'gerrymander') {
      if (!gerrymanderFrom) {
        setGerrymanderFrom(zoneId);
      } else if (gerrymanderFrom !== zoneId) {
        setSelectedZone(zoneId);
      }
    } else {
      setSelectedZone(zoneId);
    }
  };

  const handlePlaceVoters = (amount: number, volatileArea: boolean = false) => {
    if (selectedZone && amount > 0) {
      sendMessage({
        type: 'placeVoters',
        zoneId: selectedZone,
        voterCount: amount,
        volatileArea: volatileArea
      });
      setSelectedZone(null);
      setActionMode(null);
    }
  };

  const handleGerrymander = () => {
    if (gerrymanderFrom && selectedZone) {
      sendMessage({
        type: 'gerrymander',
        fromZoneId: gerrymanderFrom,
        toZoneId: selectedZone
      });
      setGerrymanderFrom(null);
      setSelectedZone(null);
      setActionMode(null);
    }
  };

  const cancelAction = () => {
    setSelectedZone(null);
    setActionMode(null);
    setGerrymanderFrom(null);
  };

  const currentPlayer = playerId ? gameState.players[playerId] : null;
  const hasGerrymanderingRights = currentPlayer?.gerrymanderingRights?.length > 0;

  const getActionModeDescription = () => {
    if (actionMode === 'place') return 'Click a zone to place voters there';
    if (actionMode === 'gerrymander') {
      if (!gerrymanderFrom) return 'Click a zone you control to move voters from';
      return 'Click an adjacent zone to move voters to';
    }
    return 'Select an action to interact with zones';
  };

  return (
    <div className="space-y-6">
      {/* Action Controls */}
      {isCurrentTurn && (
        <div className="bg-card border rounded-lg p-6 shadow-sm">
          <div className="space-y-4">
            <div className="space-y-2">
              <h3 className="text-xl font-bold tracking-tight">Board Actions</h3>
              <p className="text-sm text-muted-foreground">{getActionModeDescription()}</p>
            </div>
            
            <div className="flex flex-wrap gap-3">
              <button
                onClick={() => setActionMode('place')}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition-all duration-200 ${
                  actionMode === 'place'
                    ? 'bg-primary text-primary-foreground shadow-md'
                    : 'bg-secondary text-secondary-foreground hover:bg-secondary/80 hover:shadow-sm'
                }`}
              >
                üìç Place Voters
              </button>
              
              <button
                onClick={() => setActionMode('gerrymander')}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition-all duration-200 ${
                  actionMode === 'gerrymander'
                    ? 'bg-primary text-primary-foreground shadow-md'
                    : hasGerrymanderingRights 
                      ? 'bg-secondary text-secondary-foreground hover:bg-secondary/80 hover:shadow-sm'
                      : 'bg-muted text-muted-foreground cursor-not-allowed'
                }`}
                disabled={!hasGerrymanderingRights}
              >
                üîÑ Gerrymander 
                {hasGerrymanderingRights ? ` (${currentPlayer.gerrymanderingRights.length})` : ' (No rights)'}
              </button>
              
              {(actionMode || selectedZone) && (
                <button
                  onClick={cancelAction}
                  className="px-4 py-2 bg-destructive text-destructive-foreground rounded-lg text-sm font-medium hover:bg-destructive/90 transition-all duration-200"
                >
                  ‚úï Cancel
                </button>
              )}
            </div>
            
            {actionMode === 'gerrymander' && gerrymanderFrom && !selectedZone && (
              <div className="p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
                <p className="text-sm text-yellow-800">
                  ‚úì Source zone selected. Now click an adjacent zone to move voters to.
                </p>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Game Board */}
      <div className="bg-card border rounded-lg p-6 shadow-sm">
        <div className="space-y-4">
          <div className="space-y-2">
            <h3 className="text-xl font-bold tracking-tight">SHASN Political Map</h3>
            <p className="text-sm text-muted-foreground">9 zones representing different regions of the political landscape</p>
          </div>
          
          <div className="grid grid-cols-3 gap-4 p-4 bg-muted/10 rounded-lg">
            {gameState.zones.map((zone: any) => (
              <Zone
                key={zone.id}
                zone={zone}
                isSelected={selectedZone === zone.id}
                isGerrymanderSource={gerrymanderFrom === zone.id}
                onClick={() => handleZoneClick(zone.id)}
                canInteract={isCurrentTurn && (actionMode !== null)}
                players={gameState.players}
              />
            ))}
          </div>
          
          {!isCurrentTurn && (
            <div className="text-center p-4 bg-muted/20 rounded-lg">
              <p className="text-sm text-muted-foreground">
                Wait for your turn to interact with the board
              </p>
            </div>
          )}
        </div>
      </div>

      {/* Action Dialog */}
      {selectedZone && actionMode === 'place' && (
        <VoterPlacementDialog
          zone={gameState.zones.find((z: any) => z.id === selectedZone)}
          onConfirm={handlePlaceVoters}
          onCancel={cancelAction}
        />
      )}

      {selectedZone && actionMode === 'gerrymander' && gerrymanderFrom && (
        <GerrymanderDialog
          fromZone={gameState.zones.find((z: any) => z.id === gerrymanderFrom)}
          toZone={gameState.zones.find((z: any) => z.id === selectedZone)}
          onConfirm={handleGerrymander}
          onCancel={cancelAction}
        />
      )}
    </div>
  );
}

// Voter Placement Dialog Component
function VoterPlacementDialog({ 
  zone, 
  onConfirm, 
  onCancel 
}: {
  zone: any;
  onConfirm: (amount: number, volatileArea?: boolean) => void;
  onCancel: () => void;
}) {
  const [amount, setAmount] = useState(1);
  const [volatileArea, setVolatileArea] = useState(false);

  const maxVoters = zone.maxVoters - zone.voters.length;

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-card p-6 rounded-lg max-w-md w-full mx-4 shadow-xl border">
        <div className="space-y-4">
          <div className="space-y-2">
            <h3 className="text-xl font-bold tracking-tight">Place Voters</h3>
            <p className="text-sm text-muted-foreground">
              Deploying voters in <span className="font-semibold text-foreground">{zone.name}</span>
            </p>
          </div>
          
          <div className="p-4 bg-muted/20 rounded-lg space-y-2">
            <div className="flex justify-between text-sm">
              <span className="text-muted-foreground">Current voters:</span>
              <span className="font-medium">{zone.voters.length}/{zone.maxVoters}</span>
            </div>
            <div className="flex justify-between text-sm">
              <span className="text-muted-foreground">Majority needed:</span>
              <span className="font-medium text-primary">{zone.majorityRequired}</span>
            </div>
            <div className="flex justify-between text-sm">
              <span className="text-muted-foreground">Available slots:</span>
              <span className="font-medium">{maxVoters}</span>
            </div>
          </div>
          
          <div className="space-y-3">
            <div>
              <label className="block text-sm font-semibold mb-2">
                Number of voters to place:
              </label>
              <input
                type="number"
                min="1"
                max={maxVoters}
                value={amount}
                onChange={(e) => setAmount(Math.max(1, Math.min(maxVoters, parseInt(e.target.value) || 1)))}
                className="w-full px-3 py-2 border-2 rounded-lg bg-background focus:border-primary focus:outline-none transition-colors"
              />
            </div>

            {zone.volatileAreas && zone.volatileAreas.length > 0 && (
              <div className="p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
                <div className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    id="volatileArea"
                    checked={volatileArea}
                    onChange={(e) => setVolatileArea(e.target.checked)}
                    className="rounded"
                  />
                  <label htmlFor="volatileArea" className="text-sm font-medium text-yellow-800">
                    Place in volatile area
                  </label>
                </div>
                <p className="text-xs text-yellow-700 mt-1">
                  ‚ö†Ô∏è This will trigger a headline event
                </p>
              </div>
            )}
            
            <div className="flex space-x-3 pt-2">
              <button
                onClick={() => onConfirm(amount, volatileArea)}
                disabled={amount > maxVoters || amount < 1}
                className="flex-1 px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 disabled:opacity-50 disabled:cursor-not-allowed transition-colors font-medium"
              >
                Place {amount} Voter{amount !== 1 ? 's' : ''}
              </button>
              <button
                onClick={onCancel}
                className="flex-1 px-4 py-2 bg-secondary text-secondary-foreground rounded-lg hover:bg-secondary/80 transition-colors font-medium"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

// Gerrymander Dialog Component
function GerrymanderDialog({ 
  fromZone, 
  toZone, 
  onConfirm, 
  onCancel 
}: {
  fromZone: any;
  toZone: any;
  onConfirm: () => void;
  onCancel: () => void;
}) {
  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-card p-6 rounded-lg max-w-md w-full mx-4 shadow-xl border">
        <div className="space-y-4">
          <div className="space-y-2">
            <h3 className="text-xl font-bold tracking-tight">Gerrymander Voters</h3>
            <p className="text-sm text-muted-foreground">
              Redistribute voters between zones
            </p>
          </div>
          
          <div className="space-y-3">
            <div className="p-4 bg-muted/20 rounded-lg">
              <div className="space-y-2">
                <div className="flex justify-between items-center">
                  <span className="text-sm font-medium">From:</span>
                  <span className="font-semibold">{fromZone.name}</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm font-medium">To:</span>
                  <span className="font-semibold">{toZone.name}</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm font-medium">Moving:</span>
                  <span className="font-semibold text-primary">1 non-majority voter</span>
                </div>
              </div>
            </div>
            
            <div className="p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
              <p className="text-sm text-yellow-800">
                <span className="font-medium">Note:</span> Only non-majority voters can be moved. Voters in volatile areas cannot be gerrymandered.
              </p>
            </div>
            
            <div className="flex space-x-3 pt-2">
              <button
                onClick={onConfirm}
                className="flex-1 px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors font-medium"
              >
                Confirm Gerrymander
              </button>
              <button
                onClick={onCancel}
                className="flex-1 px-4 py-2 bg-secondary text-secondary-foreground rounded-lg hover:bg-secondary/80 transition-colors font-medium"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
