'use client';

import React, { useState } from 'react';
import { useWebSocket } from '@/hooks/useWebSocket';
import GameBoard from '@/components/GameBoard';
import ResourcePanel from '@/components/ResourcePanel';
import NegotiationPanel from '@/components/NegotiationPanel';
import IdeologyCard from '@/components/IdeologyCard';
import VoterCards from '@/components/VoterCards';
import ConnectionStatus from '@/components/ConnectionStatus';
import MoveHistoryPanel from '@/components/MoveHistoryPanel';
import ErrorBoundary from '@/components/ErrorBoundary';

export default function GamePage() {
  const { 
    gameState, 
    playerId, 
    connectionStatus, 
    lastMessage, 
    error, 
    moveHistory,
    sendMessage, 
    connect,
    currentPlayer,
    isCurrentTurn 
  } = useWebSocket();

  const [showMoveHistory, setShowMoveHistory] = useState(false);

  const handleStartGame = () => {
    sendMessage({ type: 'startGame' });
  };

  const handleEndTurn = () => {
    sendMessage({ type: 'endTurn' });
  };

  const handleAnswerIdeology = (answer: string) => {
    sendMessage({ type: 'answerIdeology', answer });
  };

  const handleRedrawIdeology = () => {
    sendMessage({ type: 'redrawIdeology' });
  };

  if (!gameState) {
    return (
      <ErrorBoundary>
        <div className="min-h-screen flex items-center justify-center bg-background text-foreground">
          <div className="text-center space-y-6 max-w-md mx-4">
            <div className="space-y-2">
              <h1 className="text-4xl font-bold tracking-tight">SHASN</h1>
              <p className="text-lg text-muted-foreground">Multiplayer Political Strategy</p>
            </div>
            
            <ConnectionStatus 
              status={connectionStatus} 
              error={error} 
              onRetry={connect}
            />
            
            <div className="space-y-2">
              <p className="text-muted-foreground">Connecting to game server...</p>
              <div className="text-xs text-muted-foreground">
                Please ensure the WebSocket server is running on port 8080
              </div>
            </div>
          </div>
        </div>
      </ErrorBoundary>
    );
  }

  return (
    <ErrorBoundary>
      <div className="min-h-screen p-4 bg-background text-foreground">
        <header className="mb-6">
          <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-4">
            <div className="space-y-1">
              <h1 className="text-4xl font-bold tracking-tight">SHASN</h1>
              <div className="flex flex-wrap items-center gap-2 text-sm text-muted-foreground">
                <span className="bg-muted px-2 py-1 rounded">
                  Players: {Object.keys(gameState.players).length}
                </span>
                <span className="bg-muted px-2 py-1 rounded">
                  Phase: {gameState.gamePhase}
                </span>
                {gameState.gamePhase === 'playing' && (
                  <span className="bg-primary/10 text-primary px-2 py-1 rounded">
                    Turn: {gameState.players[gameState.turnOrder[gameState.currentTurn]]?.name}
                    {isCurrentTurn && <span className="font-semibold"> (Your Turn)</span>}
                  </span>
                )}
              </div>
            </div>
            
            <div className="flex items-center gap-3">
              <button
                onClick={() => setShowMoveHistory(true)}
                className="px-3 py-2 text-sm bg-secondary text-secondary-foreground rounded-md hover:bg-secondary/80 transition-colors"
              >
                Move History ({moveHistory.length})
              </button>
              
              <a
                href="/api/download-all"
                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-500 transition-colors text-sm font-medium"
                target="_blank"
                rel="noopener noreferrer"
              >
                Download Project Files
              </a>
              
              <ConnectionStatus 
                status={connectionStatus} 
                error={error} 
                onRetry={connect}
              />
            </div>
          </div>
        </header>

        {/* Game End Notification */}
        {lastMessage && lastMessage.type === 'gameEnded' && (
          <div className="mb-6 p-6 bg-gradient-to-r from-primary/10 to-primary/5 border border-primary/20 rounded-lg">
            <div className="text-center space-y-2">
              <h2 className="text-2xl font-bold text-primary">üèÜ Game Over!</h2>
              <p className="text-lg">
                <span className="font-semibold">{gameState.players[lastMessage.winner]?.name}</span> wins!
              </p>
              <p className="text-muted-foreground">
                Final Score: {lastMessage.scores[lastMessage.winner]} majority voters
              </p>
            </div>
          </div>
        )}

        <div className="space-y-6">
          {/* Resource Panel */}
          <ResourcePanel 
            gameState={gameState} 
            currentPlayer={currentPlayer}
            playerId={playerId}
          />

          {/* Game Setup Phase */}
          {gameState.gamePhase === 'setup' && (
            <div className="text-center space-y-6 py-8">
              <div className="space-y-2">
                <h2 className="text-2xl font-semibold">Waiting for Players</h2>
                <p className="text-muted-foreground">
                  {Object.keys(gameState.players).length >= 2 
                    ? 'All players ready! You can start the game now.' 
                    : `Need at least 2 players to begin (${Object.keys(gameState.players).length}/2)`
                  }
                </p>
              </div>
              
              {Object.keys(gameState.players).length >= 2 && (
                <button
                  onClick={handleStartGame}
                  className="px-8 py-3 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors font-medium"
                >
                  Start Game
                </button>
              )}
            </div>
          )}

          {/* Main Game Interface */}
          {gameState.gamePhase === 'playing' && (
            <div className="grid grid-cols-1 xl:grid-cols-4 gap-6">
              {/* Game Board - Takes up most space */}
              <div className="xl:col-span-2">
                <GameBoard 
                  gameState={gameState} 
                  sendMessage={sendMessage}
                  isCurrentTurn={isCurrentTurn}
                  playerId={playerId}
                />
              </div>

              {/* Side Panel */}
              <div className="xl:col-span-2 space-y-4">
                {/* Current Ideology Card */}
                {isCurrentTurn && gameState.currentIdeologyCard && (
                  <IdeologyCard 
                    card={gameState.currentIdeologyCard}
                    onAnswer={handleAnswerIdeology}
                    onRedraw={handleRedrawIdeology}
                    currentPlayer={currentPlayer}
                  />
                )}

                {/* Voter Cards */}
                <VoterCards 
                  openCards={gameState.openVoterCards}
                  sendMessage={sendMessage}
                  currentPlayer={currentPlayer}
                  isCurrentTurn={isCurrentTurn}
                />

                {/* Negotiation Panel */}
                <NegotiationPanel 
                  sendMessage={sendMessage}
                  players={gameState.players}
                  currentPlayerId={playerId}
                />

                {/* Turn Actions */}
                {isCurrentTurn && (
                  <div className="p-4 border rounded-lg bg-card">
                    <h3 className="font-semibold mb-3">Turn Actions</h3>
                    <button
                      onClick={handleEndTurn}
                      className="w-full px-4 py-2 bg-primary text-primary-foreground rounded-md hover:bg-primary/90 transition-colors font-medium"
                    >
                      End Turn
                    </button>
                  </div>
                )}
              </div>
            </div>
          )}
        </div>

        {/* Move History Panel */}
        <MoveHistoryPanel
          isOpen={showMoveHistory}
          onClose={() => setShowMoveHistory(false)}
          moveHistory={moveHistory}
          players={gameState.players}
        />
      </div>
    </ErrorBoundary>
  );
}
