import React from 'react';

interface ConnectionStatusProps {
  status: 'disconnected' | 'connecting' | 'connected';
  error?: string | null;
  onRetry?: () => void;
}

export default function ConnectionStatus({ status, error, onRetry }: ConnectionStatusProps) {
  const getStatusColor = () => {
    switch (status) {
      case 'connected':
        return 'text-green-600';
      case 'connecting':
        return 'text-yellow-600';
      case 'disconnected':
        return 'text-red-600';
      default:
        return 'text-muted-foreground';
    }
  };

  const getStatusText = () => {
    switch (status) {
      case 'connected':
        return 'Connected';
      case 'connecting':
        return 'Connecting...';
      case 'disconnected':
        return 'Disconnected';
      default:
        return 'Unknown';
    }
  };

  const getStatusIcon = () => {
    switch (status) {
      case 'connected':
        return '‚óè';
      case 'connecting':
        return '‚óê';
      case 'disconnected':
        return '‚óã';
      default:
        return '?';
    }
  };

  return (
    <div className="flex items-center space-x-3">
      {/* Status Indicator */}
      <div className="flex items-center space-x-2">
        <span className={`text-sm ${getStatusColor()}`}>
          {getStatusIcon()}
        </span>
        <span className={`text-sm font-medium ${getStatusColor()}`}>
          {getStatusText()}
        </span>
      </div>

      {/* Retry Button */}
      {status === 'disconnected' && onRetry && (
        <button
          onClick={onRetry}
          className="px-2 py-1 text-xs bg-primary text-primary-foreground rounded hover:bg-primary/90 transition-colors"
        >
          Retry
        </button>
      )}

      {/* Error Display */}
      {error && (
        <div className="max-w-xs">
          <div className="px-2 py-1 bg-destructive/10 border border-destructive/20 rounded text-xs text-destructive">
            {error}
          </div>
        </div>
      )}
    </div>
  );
}
