'use client';

import React from 'react';

interface MoveHistoryEvent {
  id: string;
  timestamp: number;
  type: string;
  description: string;
  playerId?: string;
  playerName?: string;
  details?: any;
}

interface MoveHistoryPanelProps {
  isOpen: boolean;
  onClose: () => void;
  moveHistory: MoveHistoryEvent[];
  players: Record<string, any>;
}

export default function MoveHistoryPanel({ 
  isOpen, 
  onClose, 
  moveHistory, 
  players 
}: MoveHistoryPanelProps) {
  if (!isOpen) return null;

  const formatTime = (timestamp: number) => {
    const date = new Date(timestamp);
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit', second: '2-digit' });
  };

  const getEventIcon = (type: string) => {
    const icons: Record<string, string> = {
      'gameStarted': 'üéÆ',
      'ideologyAnswered': 'üí≠',
      'votersInfluenced': 'üë•',
      'votersPlaced': 'üìç',
      'gerrymandered': 'üîÑ',
      'turnEnded': '‚è≠Ô∏è',
      'playerJoined': 'üëã',
      'playerDisconnected': 'üëã',
      'negotiate': 'ü§ù',
      'headlineTriggered': 'üì∞',
      'gameEnded': 'üèÜ',
      'error': '‚ö†Ô∏è'
    };
    return icons[type] || 'üìù';
  };

  const getEventColor = (type: string) => {
    const colors: Record<string, string> = {
      'gameStarted': 'text-green-600',
      'ideologyAnswered': 'text-blue-600',
      'votersInfluenced': 'text-purple-600',
      'votersPlaced': 'text-orange-600',
      'gerrymandered': 'text-yellow-600',
      'turnEnded': 'text-gray-600',
      'playerJoined': 'text-green-500',
      'playerDisconnected': 'text-red-500',
      'negotiate': 'text-cyan-600',
      'headlineTriggered': 'text-red-600',
      'gameEnded': 'text-gold-600',
      'error': 'text-destructive'
    };
    return colors[type] || 'text-muted-foreground';
  };

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <div className="bg-card border rounded-lg max-w-2xl w-full mx-4 max-h-[80vh] flex flex-col">
        {/* Header */}
        <div className="flex justify-between items-center p-4 border-b">
          <h2 className="text-lg font-semibold">Move History</h2>
          <button
            onClick={onClose}
            className="text-muted-foreground hover:text-foreground transition-colors"
          >
            ‚úï
          </button>
        </div>

        {/* History List */}
        <div className="flex-1 overflow-y-auto p-4">
          {moveHistory.length === 0 ? (
            <div className="text-center text-muted-foreground py-8">
              <p>No moves recorded yet</p>
              <p className="text-sm mt-2">Game events will appear here as they happen</p>
            </div>
          ) : (
            <div className="space-y-3">
              {moveHistory.slice().reverse().map((event) => (
                <div
                  key={event.id}
                  className="flex items-start space-x-3 p-3 bg-muted/30 rounded-md hover:bg-muted/50 transition-colors"
                >
                  {/* Event Icon */}
                  <div className="text-lg flex-shrink-0 mt-0.5">
                    {getEventIcon(event.type)}
                  </div>

                  {/* Event Content */}
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center justify-between mb-1">
                      <span className={`text-sm font-medium ${getEventColor(event.type)}`}>
                        {event.type.replace(/([A-Z])/g, ' $1').replace(/^./, str => str.toUpperCase())}
                      </span>
                      <span className="text-xs text-muted-foreground">
                        {formatTime(event.timestamp)}
                      </span>
                    </div>
                    
                    <p className="text-sm text-foreground">
                      {event.description}
                    </p>

                    {event.playerName && (
                      <p className="text-xs text-muted-foreground mt-1">
                        Player: {event.playerName}
                      </p>
                    )}

                    {event.details && (
                      <div className="text-xs text-muted-foreground mt-1 bg-muted/50 p-2 rounded">
                        <pre className="whitespace-pre-wrap">
                          {typeof event.details === 'string' 
                            ? event.details 
                            : JSON.stringify(event.details, null, 2)
                          }
                        </pre>
                      </div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Footer */}
        <div className="p-4 border-t bg-muted/20">
          <div className="flex justify-between items-center text-xs text-muted-foreground">
            <span>{moveHistory.length} events recorded</span>
            <span>Most recent events shown first</span>
          </div>
        </div>
      </div>
    </div>
  );
}
