import React from 'react';

interface ZoneProps {
  zone: any;
  isSelected: boolean;
  isGerrymanderSource: boolean;
  onClick: () => void;
  canInteract: boolean;
  players: Record<string, any>;
}

export default function Zone({ 
  zone, 
  isSelected, 
  isGerrymanderSource, 
  onClick, 
  canInteract, 
  players 
}: ZoneProps) {
  const getMajorityPlayerName = () => {
    // Count voters by player
    const playerVoterCounts: Record<string, number> = {};
    zone.voters.forEach((voter: any) => {
      playerVoterCounts[voter.playerId] = (playerVoterCounts[voter.playerId] || 0) + 1;
    });
    
    // Find player with majority
    const majorityPlayer = Object.entries(playerVoterCounts).find(
      ([playerId, count]) => count >= zone.majorityRequired
    );
    
    if (majorityPlayer && players[majorityPlayer[0]]) {
      return players[majorityPlayer[0]].name;
    }
    return null;
  };

  const getMajorityVoters = () => {
    const playerVoterCounts: Record<string, number> = {};
    zone.voters.forEach((voter: any) => {
      if (voter.isMajority) {
        playerVoterCounts[voter.playerId] = (playerVoterCounts[voter.playerId] || 0) + 1;
      }
    });
    return playerVoterCounts;
  };

  const getPlayerVoterCounts = () => {
    const playerVoterCounts: Record<string, number> = {};
    zone.voters.forEach((voter: any) => {
      playerVoterCounts[voter.playerId] = (playerVoterCounts[voter.playerId] || 0) + 1;
    });
    return playerVoterCounts;
  };

  const getZoneClasses = () => {
    let classes = 'p-4 border-2 rounded-xl transition-all duration-300 cursor-pointer relative overflow-hidden ';
    
    if (isSelected) {
      classes += 'border-primary bg-primary/10 shadow-lg scale-105 ';
    } else if (isGerrymanderSource) {
      classes += 'border-yellow-500 bg-yellow-500/10 shadow-md ';
    } else if (zone.volatileAreas && zone.volatileAreas.length > 0) {
      classes += 'border-destructive/50 bg-destructive/5 ';
    } else {
      classes += 'border-border bg-card hover:bg-muted/20 ';
    }
    
    if (canInteract) {
      classes += 'hover:border-primary/50 hover:shadow-md hover:scale-102 ';
    } else {
      classes += 'opacity-60 cursor-not-allowed ';
    }
    
    // Check if any player has majority
    const majorityVoters = getMajorityVoters();
    if (Object.keys(majorityVoters).length > 0) {
      classes += 'ring-2 ring-primary/30 ';
    }
    
    return classes;
  };

  const majorityPlayerName = getMajorityPlayerName();
  const playerVoterCounts = getPlayerVoterCounts();
  const hasVoters = zone.voters.length > 0;
  const isFull = zone.voters.length >= zone.maxVoters;
  const progressPercentage = (zone.voters.length / zone.maxVoters) * 100;

  return (
    <div
      className={getZoneClasses()}
      onClick={canInteract ? onClick : undefined}
    >
      {/* Background Pattern */}
      <div className="absolute inset-0 opacity-5">
        <div className="w-full h-full bg-gradient-to-br from-primary/20 to-transparent"></div>
      </div>

      <div className="relative space-y-3">
        {/* Zone Header */}
        <div className="flex justify-between items-start">
          <h3 className="font-bold text-sm text-foreground">{zone.name}</h3>
          <div className="flex flex-col items-end space-y-1">
            {zone.volatileAreas && zone.volatileAreas.length > 0 && (
              <span className="text-xs bg-destructive text-destructive-foreground px-2 py-1 rounded-full font-medium">
                ‚ö†Ô∏è Volatile
              </span>
            )}
            {isFull && (
              <span className="text-xs bg-orange-100 text-orange-800 px-2 py-1 rounded-full font-medium">
                Full
              </span>
            )}
          </div>
        </div>

        {/* Voter Count Display */}
        <div className="text-center space-y-2">
          <div className="relative">
            <div className="text-3xl font-bold text-primary">
              {zone.voters.length}
            </div>
            <div className="text-xs text-muted-foreground">
              of {zone.maxVoters} voters
            </div>
          </div>
          
          {/* Progress Bar */}
          <div className="w-full bg-muted rounded-full h-2">
            <div 
              className="bg-primary h-2 rounded-full transition-all duration-500"
              style={{ width: `${progressPercentage}%` }}
            ></div>
          </div>
          
          <div className="text-xs text-muted-foreground">
            Need <span className="font-semibold text-primary">{zone.majorityRequired}</span> for majority
          </div>
        </div>

        {/* Player Distribution */}
        {hasVoters && (
          <div className="space-y-2">
            <div className="text-xs font-medium text-muted-foreground text-center">
              Player Distribution
            </div>
            <div className="space-y-1">
              {Object.entries(playerVoterCounts).map(([playerId, count]) => (
                <div key={playerId} className="flex justify-between items-center text-xs">
                  <span className={`font-medium ${
                    majorityPlayerName === players[playerId]?.name ? 'text-primary' : 'text-muted-foreground'
                  }`}>
                    {players[playerId]?.name || 'Unknown'}
                  </span>
                  <span className={`font-bold ${
                    count >= zone.majorityRequired ? 'text-primary' : 'text-foreground'
                  }`}>
                    {count}
                  </span>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Majority Control */}
        <div className="text-center">
          <div className="text-xs text-muted-foreground mb-1">
            Zone Control
          </div>
          <div className={`text-sm font-bold ${
            majorityPlayerName ? 'text-primary' : 'text-muted-foreground'
          }`}>
            {majorityPlayerName || 'Contested'}
          </div>
        </div>

        {/* Zone Status Indicators */}
        <div className="flex justify-center space-x-1">
          {isSelected && (
            <span className="text-xs bg-primary text-primary-foreground px-2 py-1 rounded-full font-medium animate-pulse">
              Selected
            </span>
          )}
          {isGerrymanderSource && (
            <span className="text-xs bg-yellow-500 text-white px-2 py-1 rounded-full font-medium">
              Source
            </span>
          )}
        </div>

        {/* Interactive Hint */}
        {canInteract && (
          <div className="text-xs text-center text-primary font-medium opacity-75">
            Click to interact ‚Üí
          </div>
        )}
        
        {!canInteract && !isSelected && !isGerrymanderSource && (
          <div className="text-xs text-center text-muted-foreground">
            {zone.voters.length === 0 ? 'No voters placed' : 'Zone occupied'}
          </div>
        )}
      </div>
    </div>
  );
}
