import React from 'react';

interface VoterCardsProps {
  openCards: any[];
  sendMessage: (msg: any) => void;
  currentPlayer: any;
  isCurrentTurn: boolean;
}

export default function VoterCards({ openCards, sendMessage, currentPlayer, isCurrentTurn }: VoterCardsProps) {
  if (!openCards || openCards.length === 0) {
    return (
      <div className="bg-card border rounded-lg p-6 shadow-sm">
        <div className="space-y-2 mb-4">
          <h3 className="text-xl font-bold tracking-tight">Voter Cards</h3>
          <p className="text-sm text-muted-foreground">Available influence opportunities</p>
        </div>
        <div className="text-center py-8">
          <div className="text-4xl mb-2">‚è≥</div>
          <p className="text-sm text-muted-foreground">Loading voter cards...</p>
        </div>
      </div>
    );
  }

  const canAffordCard = (card: any) => {
    if (!currentPlayer || !card.cost) return false;
    
    return Object.entries(card.cost).every(([resource, amount]) => 
      currentPlayer.resources[resource] >= (amount as number)
    );
  };

  const handleInfluenceVoters = (cardId: number) => {
    if (isCurrentTurn && canAffordCard(openCards.find(c => c.id === cardId))) {
      sendMessage({ type: 'influenceVoters', cardId });
    }
  };

  const getResourceCostDisplay = (cost: any) => {
    return Object.entries(cost).map(([resource, amount]) => (
      <span key={resource} className="inline-block bg-muted text-muted-foreground px-2 py-1 rounded-md text-xs font-medium">
        {resource}: {amount as number}
      </span>
    ));
  };

  const getCardStatusColor = (card: any) => {
    if (!isCurrentTurn) return 'border-muted bg-muted/10';
    if (canAffordCard(card)) return 'border-primary/30 bg-primary/5 hover:border-primary/50 hover:bg-primary/10';
    return 'border-destructive/30 bg-destructive/5';
  };

  const getCardStatusText = (card: any) => {
    if (!isCurrentTurn) return 'Wait for your turn';
    if (!canAffordCard(card)) return 'Insufficient resources';
    return 'Click to influence voters';
  };

  return (
    <div className="bg-card border rounded-lg p-6 shadow-sm">
      <div className="space-y-2 mb-6">
        <h3 className="text-xl font-bold tracking-tight">Voter Cards</h3>
        <p className="text-sm text-muted-foreground">Available influence opportunities</p>
      </div>
      
      <div className="space-y-4">
        {openCards.map((card) => (
          <div
            key={card.id}
            className={`p-4 border-2 rounded-lg transition-all duration-200 ${getCardStatusColor(card)} ${
              canAffordCard(card) && isCurrentTurn ? 'cursor-pointer' : 'cursor-not-allowed'
            }`}
            onClick={() => handleInfluenceVoters(card.id)}
          >
            <div className="flex justify-between items-start mb-3">
              <div className="flex items-center space-x-3">
                <div className="text-center">
                  <div className="text-2xl font-bold text-primary">{card.voters}</div>
                  <div className="text-xs text-muted-foreground">
                    {card.voters === 1 ? 'Voter' : 'Voters'}
                  </div>
                </div>
                <div className="text-sm text-muted-foreground">
                  Influence {card.voters} voter{card.voters !== 1 ? 's' : ''} to support your cause
                </div>
              </div>
              
              <div className="flex flex-wrap gap-2">
                {getResourceCostDisplay(card.cost)}
              </div>
            </div>
            
            <div className="flex justify-between items-center">
              <div className="text-xs text-muted-foreground">
                {getCardStatusText(card)}
              </div>
              
              {canAffordCard(card) && isCurrentTurn && (
                <div className="text-xs text-primary font-medium">
                  Ready to influence ‚Üí
                </div>
              )}
            </div>
          </div>
        ))}
      </div>

      <div className="mt-6 p-4 bg-muted/20 rounded-lg">
        <div className="space-y-2">
          <p className="text-xs font-medium text-foreground">How to Use Voter Cards:</p>
          <ul className="text-xs text-muted-foreground space-y-1">
            <li>‚Ä¢ Pay the required resources to influence voters</li>
            <li>‚Ä¢ Place all voters from a card in a single zone</li>
            <li>‚Ä¢ Strategic placement can help you gain zone majorities</li>
            <li>‚Ä¢ New cards appear when others are used</li>
          </ul>
        </div>
      </div>
    </div>
  );
}
