import React from 'react';

interface ResourcePanelProps {
  gameState: any;
  currentPlayer: any;
  playerId: string | null;
}

export default function ResourcePanel({ gameState, currentPlayer, playerId }: ResourcePanelProps) {
  if (!gameState || !currentPlayer) {
    return null;
  }

  const allPlayers = Object.values(gameState.players);

  const getResourceBadge = (resource: string) => {
    const badges = {
      funds: { label: 'Funds', color: 'bg-blue-100 text-blue-800' },
      clout: { label: 'Clout', color: 'bg-red-100 text-red-800' },
      media: { label: 'Media', color: 'bg-purple-100 text-purple-800' },
      trust: { label: 'Trust', color: 'bg-green-100 text-green-800' }
    };
    return badges[resource as keyof typeof badges] || { label: resource, color: 'bg-gray-100 text-gray-800' };
  };

  const getResourceName = (resource: string) => {
    const names = {
      funds: 'Campaign Funds',
      clout: 'Street Clout',
      media: 'Media Attention', 
      trust: 'Public Trust'
    };
    return names[resource as keyof typeof names] || resource;
  };

  const getMajorityPlayer = (zone: any) => {
    const playerVoterCounts: Record<string, number> = {};
    zone.voters.forEach((voter: any) => {
      playerVoterCounts[voter.playerId] = (playerVoterCounts[voter.playerId] || 0) + 1;
    });
    
    const majorityPlayer = Object.entries(playerVoterCounts).find(
      ([playerId, count]) => count >= zone.majorityRequired
    );
    
    if (majorityPlayer && gameState.players[majorityPlayer[0]]) {
      return gameState.players[majorityPlayer[0]].name;
    }
    return null;
  };

  return (
    <div className="bg-card border rounded-lg p-6 shadow-sm">
      <div className="space-y-2 mb-6">
        <h2 className="text-2xl font-bold tracking-tight">Player Resources</h2>
        <p className="text-sm text-muted-foreground">Current game status and player standings</p>
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 2xl:grid-cols-4 gap-4 mb-6">
        {allPlayers.map((player: any) => (
          <div
            key={player.id}
            className={`p-4 border-2 rounded-lg transition-all ${
              player.id === playerId 
                ? 'border-primary bg-primary/5 shadow-md' 
                : 'border-border hover:border-muted-foreground/30'
            }`}
          >
            <div className="flex items-center justify-between mb-3">
              <h3 className={`font-bold text-lg ${
                player.id === playerId ? 'text-primary' : 'text-foreground'
              }`}>
                {player.name}
                {player.id === playerId && <span className="text-xs ml-2 font-normal">(You)</span>}
              </h3>
              {gameState.turnOrder[gameState.currentTurn] === player.id && (
                <span className="text-xs bg-primary text-primary-foreground px-2 py-1 rounded-full font-medium">
                  Active Turn
                </span>
              )}
            </div>
            
            <div className="space-y-3">
              {/* SHASN Resources */}
              <div className="space-y-2">
                <h4 className="text-xs font-semibold text-muted-foreground uppercase tracking-wide">Resources</h4>
                <div className="grid grid-cols-2 gap-2">
                  {Object.entries(player.resources || {}).map(([resource, amount]) => {
                    const badge = getResourceBadge(resource);
                    return (
                      <div key={resource} className="flex items-center justify-between">
                        <span className={`text-xs px-2 py-1 rounded-full font-medium ${badge.color}`}>
                          {badge.label}
                        </span>
                        <span className="font-bold text-sm">{amount as number}</span>
                      </div>
                    );
                  })}
                </div>
              </div>
              
              {/* Key Stats */}
              <div className="space-y-2">
                <h4 className="text-xs font-semibold text-muted-foreground uppercase tracking-wide">Stats</h4>
                <div className="space-y-1">
                  <div className="flex justify-between text-sm">
                    <span className="text-muted-foreground">Resource Cap:</span>
                    <span className="font-medium">{player.resourceCap || 12}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-muted-foreground">Majority Voters:</span>
                    <span className="font-bold text-primary">{player.majorityVoters || 0}</span>
                  </div>
                </div>
              </div>

              {/* Ideology Cards */}
              {player.ideologyCards && player.ideologyCards.length > 0 && (
                <div className="space-y-2">
                  <h4 className="text-xs font-semibold text-muted-foreground uppercase tracking-wide">Ideologies</h4>
                  <div className="flex flex-wrap gap-1">
                    {Object.entries(player.ideologuePowers || {}).map(([ideologue, power]: [string, any]) => (
                      power.cards > 0 && (
                        <span
                          key={ideologue}
                          className="text-xs bg-secondary text-secondary-foreground px-2 py-1 rounded-full font-medium"
                        >
                          {ideologue.charAt(0).toUpperCase() + ideologue.slice(1)}: {power.cards}
                        </span>
                      )
                    ))}
                  </div>
                </div>
              )}

              {/* Gerrymandering Rights */}
              {player.gerrymanderingRights && player.gerrymanderingRights.length > 0 && (
                <div className="space-y-1">
                  <h4 className="text-xs font-semibold text-muted-foreground uppercase tracking-wide">Special Powers</h4>
                  <div className="text-sm">
                    <span className="bg-yellow-100 text-yellow-800 px-2 py-1 rounded-full text-xs font-medium">
                      Gerrymander: {player.gerrymanderingRights.length} zones
                    </span>
                  </div>
                </div>
              )}
            </div>
          </div>
        ))}
      </div>

      {/* Zone Control Summary */}
      <div className="pt-6 border-t">
        <div className="space-y-2 mb-4">
          <h3 className="text-xl font-bold tracking-tight">Board Control</h3>
          <p className="text-sm text-muted-foreground">Zone status and majority control</p>
        </div>
        
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-3">
          {gameState.zones.map((zone: any) => {
            const majorityPlayer = getMajorityPlayer(zone);
            return (
              <div key={zone.id} className="p-3 border rounded-lg bg-muted/20 hover:bg-muted/30 transition-colors">
                <div className="space-y-2">
                  <div className="flex justify-between items-start">
                    <h4 className="font-semibold text-sm">{zone.name}</h4>
                    {zone.volatileAreas && zone.volatileAreas.length > 0 && (
                      <span className="text-xs bg-destructive text-destructive-foreground px-2 py-1 rounded-full">
                        Volatile
                      </span>
                    )}
                  </div>
                  
                  <div className="text-xs space-y-1">
                    <div className="flex justify-between">
                      <span className="text-muted-foreground">Voters:</span>
                      <span className="font-medium">{zone.voters.length}/{zone.maxVoters}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-muted-foreground">Majority:</span>
                      <span className="font-medium">{zone.majorityRequired}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-muted-foreground">Control:</span>
                      <span className={`font-medium ${majorityPlayer ? 'text-primary' : 'text-muted-foreground'}`}>
                        {majorityPlayer || 'None'}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
